<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="120px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="tuijianbiaoti" >
					<el-input v-model="ruleForm.tuijianbiaoti" placeholder="" clearable  :readonly="ro.tuijianbiaoti"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="tuijianbiaoti" >
					<el-input v-model="ruleForm.tuijianbiaoti" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.tuijiantupian" label="" prop="tuijiantupian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.tuijiantupian?ruleForm.tuijiantupian:''"
						@change="tuijiantupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.tuijiantupian" label="" prop="tuijiantupian" >
					<img v-if="ruleForm.tuijiantupian.substring(0,4)=='http'&&ruleForm.tuijiantupian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tuijiantupian" width="100" height="100">
					<img v-else-if="ruleForm.tuijiantupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tuijiantupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tuijiantupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'" label="" prop="schoolname" >
					<el-select :disabled="ro.schoolname" @change="schoolnameChange" v-model="ruleForm.schoolname" placeholder="">
						<el-option
							v-for="(item,index) in schoolnameOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.schoolname" label="" prop="schoolname" >
					<el-input v-model="ruleForm.schoolname" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="specname" >
					<el-input v-model="ruleForm.specname" placeholder="" clearable  :readonly="ro.specname"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="specname" >
					<el-input v-model="ruleForm.specname" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="speccode" >
					<el-input v-model="ruleForm.speccode" placeholder="" clearable  :readonly="ro.speccode"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="speccode" >
					<el-input v-model="ruleForm.speccode" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="diqu" >
					<el-input v-model="ruleForm.diqu" placeholder="" clearable  :readonly="ro.diqu"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="diqu" >
					<el-input v-model="ruleForm.diqu" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="yuanxiaocengci" >
					<el-select :disabled="ro.yuanxiaocengci" v-model="ruleForm.yuanxiaocengci" placeholder="" >
						<el-option
							v-for="(item,index) in yuanxiaocengciOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yuanxiaocengci" >
					<el-input v-model="ruleForm.yuanxiaocengci"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="luqufenshuxian" >
					<el-input-number v-model="ruleForm.luqufenshuxian" placeholder="" :disabled="ro.luqufenshuxian"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="luqufenshuxian" >
					<el-input v-model="ruleForm.luqufenshuxian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="luqubili" >
					<el-input v-model="ruleForm.luqubili" placeholder="" clearable  :readonly="ro.luqubili"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="luqubili" >
					<el-input v-model="ruleForm.luqubili" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="tuijianshijian" >
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.tuijianshijian" 
						type="datetime"
						:readonly="ro.tuijianshijian"
						placeholder=""
					></el-date-picker>
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.tuijianshijian" label="" prop="tuijianshijian" >
					<el-input v-model="ruleForm.tuijianshijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="" clearable  :readonly="ro.zhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="" clearable  :readonly="ro.xingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item v-if="type!='info'"  label="" prop="tuijianxiangqing" >
				<editor 
					style="min-width: 200px; max-width: 600px;"
					v-model="ruleForm.tuijianxiangqing" 
					class="editor"
					myQuillEditor="tuijianxiangqing"
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.tuijianxiangqing" label="" prop="tuijianxiangqing" >
				<span class="text ql-snow ql-editor" v-html="ruleForm.tuijianxiangqing"></span>
			</el-form-item>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isNumber,
	} from "@/utils/validate";
	export default {
		data() {
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					tuijianbiaoti : false,
					tuijiantupian : false,
					schoolname : false,
					specname : false,
					speccode : false,
					diqu : false,
					yuanxiaocengci : false,
					luqufenshuxian : false,
					luqubili : false,
					tuijianshijian : false,
					tuijianxiangqing : false,
					zhanghao : false,
					xingming : false,
				},
			
				ruleForm: {
					tuijianbiaoti: '',
					tuijiantupian: '',
					schoolname: '',
					specname: '',
					speccode: '',
					diqu: '',
					yuanxiaocengci: '',
					luqufenshuxian: '',
					luqubili: '',
					tuijianshijian: '',
					tuijianxiangqing: '',
					zhanghao: '',
					xingming: '',
				},
				schoolnameOptions: [],
				yuanxiaocengciOptions: [],

				rules: {
					tuijianbiaoti: [
					],
					tuijiantupian: [
					],
					schoolname: [
					],
					specname: [
					],
					speccode: [
					],
					diqu: [
					],
					yuanxiaocengci: [
					],
					luqufenshuxian: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					luqubili: [
					],
					tuijianshijian: [
					],
					tuijianxiangqing: [
					],
					zhanghao: [
					],
					xingming: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='tuijianbiaoti'){
							this.ruleForm.tuijianbiaoti = obj[o];
							this.ro.tuijianbiaoti = true;
							continue;
						}
						if(o=='tuijiantupian'){
							this.ruleForm.tuijiantupian = obj[o];
							this.ro.tuijiantupian = true;
							continue;
						}
						if(o=='schoolname'){
							this.ruleForm.schoolname = obj[o];
							this.ro.schoolname = true;
							continue;
						}
						if(o=='specname'){
							this.ruleForm.specname = obj[o];
							this.ro.specname = true;
							continue;
						}
						if(o=='speccode'){
							this.ruleForm.speccode = obj[o];
							this.ro.speccode = true;
							continue;
						}
						if(o=='diqu'){
							this.ruleForm.diqu = obj[o];
							this.ro.diqu = true;
							continue;
						}
						if(o=='yuanxiaocengci'){
							this.ruleForm.yuanxiaocengci = obj[o];
							this.ro.yuanxiaocengci = true;
							continue;
						}
						if(o=='luqufenshuxian'){
							this.ruleForm.luqufenshuxian = obj[o];
							this.ro.luqufenshuxian = true;
							continue;
						}
						if(o=='luqubili'){
							this.ruleForm.luqubili = obj[o];
							this.ro.luqubili = true;
							continue;
						}
						if(o=='tuijianshijian'){
							this.ruleForm.tuijianshijian = obj[o];
							this.ro.tuijianshijian = true;
							continue;
						}
						if(o=='tuijianxiangqing'){
							this.ruleForm.tuijianxiangqing = obj[o];
							this.ro.tuijianxiangqing = true;
							continue;
						}
						if(o=='zhanghao'){
							this.ruleForm.zhanghao = obj[o];
							this.ro.zhanghao = true;
							continue;
						}
						if(o=='xingming'){
							this.ruleForm.xingming = obj[o];
							this.ro.xingming = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/hisyearscore/schoolname`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.schoolnameOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
				this.yuanxiaocengciOptions = "985,211,".split(',')
			
			},
			// 
			schoolnameChange () {
				this.$http({
					url: `follow/hisyearscore/schoolname?columnValue=`+ this.ruleForm.schoolname,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						if(data.data.specname){
							this.ruleForm.specname = data.data.specname
						}
						if(data.data.speccode){
							this.ruleForm.speccode = data.data.speccode
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			},
			// 

			info(id) {
				this.$http({
					url: `tuijianyuanxiao/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
						this.ruleForm.tuijianxiangqing = this.ruleForm.tuijianxiangqing.replace(reg,'../../../python75f6hd0t/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.tuijiantupian!=null) {
						this.ruleForm.tuijiantupian = this.ruleForm.tuijiantupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `tuijianyuanxiao/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.tuijianyuanxiaoCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.tuijianyuanxiaoCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			tuijiantupianUploadChange(fileUrls) {
				this.ruleForm.tuijiantupian = fileUrls;
			},
		}
	};
</script>
<style lang="scss" scoped>
