<script>
	import { 
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					specname : false,
					speccode : false,
					schoolname : false,
					nianfen : false,
					departname : false,
					specremark : false,
					totalscore : false,
					politics : false,
					english : false,
					specialone : false,
					specialtwo : false,
					degreetype : false,
					thumbsupnum : false,
					crazilynum : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					storeupnum : false,
				},
			
				ruleForm: {
					specname: '',
					speccode: '',
					schoolname: '',
					nianfen: '',
					departname: '',
					specremark: '',
					totalscore: '',
					politics: '',
					english: '',
					specialone: '',
					specialtwo: '',
					degreetype: '',
					clicktime: '',
				},

				rules: {
					specname: [
					],
					speccode: [
					],
					schoolname: [
					],
					nianfen: [
					],
					departname: [
					],
					specremark: [
					],
					totalscore: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					politics: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					english: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					specialone: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					specialtwo: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					degreetype: [
					],
					thumbsupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					crazilynum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					clicktime: [
					],
					clicknum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					discussnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='specname'){
							this.ruleForm.specname = obj[o];
							this.ro.specname = true;
							continue;
						}
						if(o=='speccode'){
							this.ruleForm.speccode = obj[o];
							this.ro.speccode = true;
							continue;
						}
						if(o=='schoolname'){
							this.ruleForm.schoolname = obj[o];
							this.ro.schoolname = true;
							continue;
						}
						if(o=='nianfen'){
							this.ruleForm.nianfen = obj[o];
							this.ro.nianfen = true;
							continue;
						}
						if(o=='departname'){
							this.ruleForm.departname = obj[o];
							this.ro.departname = true;
							continue;
						}
						if(o=='specremark'){
							this.ruleForm.specremark = obj[o];
							this.ro.specremark = true;
							continue;
						}
						if(o=='totalscore'){
							this.ruleForm.totalscore = obj[o];
							this.ro.totalscore = true;
							continue;
						}
						if(o=='politics'){
							this.ruleForm.politics = obj[o];
							this.ro.politics = true;
							continue;
						}
						if(o=='english'){
							this.ruleForm.english = obj[o];
							this.ro.english = true;
							continue;
						}
						if(o=='specialone'){
							this.ruleForm.specialone = obj[o];
							this.ro.specialone = true;
							continue;
						}
						if(o=='specialtwo'){
							this.ruleForm.specialtwo = obj[o];
							this.ro.specialtwo = true;
							continue;
						}
						if(o=='degreetype'){
							this.ruleForm.degreetype = obj[o];
							this.ro.degreetype = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}
			
			},
			// 

			info(id) {
				this.$http({
					url: `hisyearscore/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `hisyearscore/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.hisyearscoreCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.hisyearscoreCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
		}
	};
</script>
